$yellow : #fedc37;
$gray: #646464;
$gray-light: #f5f5f5;
$white: #ffffff;
$green-light:#ccff99;


@import '../fonts/icomoon/style.css';

[mag-run-menu-component]{
    display: none;
}
[mag-run-menu-component].logged-in{
    display: inline-block;
}


/** Helpers **/

.text-center{
    text-align: center !important;
}
.text-upper{
    text-transform: uppercase ;
}

.mx-auto{
    margin:0px auto;
}
.mt-1{
    margin-top:1rem;
}
.mt-2{
    margin-top:2rem;
}
.mt-3{
    margin-top:3rem;
}
.mt-4{
    margin-top:4rem;
}
.mt-5{
    margin-top:5rem;
}
.mb-1{
    margin-bottom:1rem;
}
.mb-2{
    margin-bottom:2rem;
}
.mb-3{
    margin-bottom:3rem;
}
.mb-4{
    margin-bottom:4rem;
}
.mb-5{
    margin-bottom:5rem;
}

/** Colors **/
.text-yellow{ color:$yellow !important;}
.text-gray{ color:$gray !important;}
.bkg-yellow{ background-color:$yellow; }

.bkg-gray-light{ background-color: $gray-light; }
.bkg-white{ background-color: #fff !important;}

/** Beetle **/@at-root
.beetle{
    width:100px;
    height:100px;
    display:block;
    font-size:63px;
    display:flex;
    justify-content:center;
    align-items:center;
}
.beetle-circle{
    border-radius: 100%;   
}



.mg{
    &-h1{
        font-family: 'Lato';
        font-size: 63px;
        font-weight: bold;
    }
    &-h2{
        font-family: 'Lato';
        font-size: 30px;
        font-weight: bold;
    }
    &-h4{
        font-family: 'Lato';
        font-size: 20px;
        font-weight: normal;
    }
    &-display-1{
        font-family: "Barlow Condensed";
        font-size:87px;
        font-weight: bold;
    }
    &-btn{
	    padding:5px 10px;
	    font-size: 1rem;
	    text-transform: uppercase;
	    &.btn-xl{
            font-family: "Barlow Condensed";
		    border-radius: 30px;
		    font-size: 2rem;
		    padding:10px 40px;
	    }
    }
    &-default{
        font-family: Roboto, Arial, Helvetica, sans-serif;        
    }
    &-input{
        border: none;
        outline: none;
        height:auto !important;
        padding: 1.5rem !important;
        font-size: 1.2rem !important;
        text-align: center;
        background: white;
        border-radius: 10px;     
        color:$gray;   
    }
}



/** Elements **/

.horizontal-section-bar{
    background: $gray;
    color:$white;
    padding:20px;
    text-align: center;
	margin: 20px auto;
    font-family: "Barlow Condensed";
}


/** Overrides **/ 


// Athlete Profile
.page-template-page-athlete-profile {
	
	.fusion-page-title-bar{
    display: none;
   }
   #main{
	   padding-right:0px;
	   padding-left:0px;
		 .fusion-row {
			 max-width: 100%;
		}
  }
}

.this-week-goal{
    &.success{
        background:$green-light;
    }
}


/** Event Calender **/

.fc-button{
    background:none;
    border:none;
}
.fc-day.fc-widget-content.has-events{
	background-color:$green-light;
	
}


